import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useLibraryUpdateBookReview } from "../../../Hooks/ApiHooks/LibraryManager/library-manager-api";
import style from "../../FinanceManager/Income/Income.module.css";
import Modal from "../../../Ui/Modal/Modal";
import {
  assestsFeedbackUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import FormTextarea from "../../../Ui/Input/FormTextarea";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";

const AddBookReview = ({ onClose, onRefetch, bid, sid }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    description: "",
    rating: 0,
    title: "",
  });
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [rating, setRating] = useState([
    `${assestsFeedbackUrl}/grey-star.svg`,
    `${assestsFeedbackUrl}/grey-star.svg`,
    `${assestsFeedbackUrl}/grey-star.svg`,
    `${assestsFeedbackUrl}/grey-star.svg`,
    `${assestsFeedbackUrl}/grey-star.svg`,
  ]);

  const [libraryUpdateBookReview] = useLibraryUpdateBookReview();

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onRating = (index) => {
    let inp = [];
    for (let i = 0; i < 5; i++) {
      if (i <= index) inp.push(`${assestsFeedbackUrl}/yellow-star.svg`);
      else inp.push(`${assestsFeedbackUrl}/grey-star.svg`);
    }
    setState((prev) => ({
      ...prev,
      rating: index + 1,
    }));
    setRating(() => inp);
  };

  const onReviewUpdate = () => {
    let validation = requiredField(state, ["rating", "title"]);
    if (existingRequiredField(validation) && sid && bid) {
      setDisabled((pre) => !pre);
      libraryUpdateBookReview({
        bid: bid,
        reviewData: {
          ...state,
          sid: sid,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6> {t("book_review")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormTextarea
              label={t("description")}
              placeholder={t("description_placeholder")}
              name={"description"}
              value={state.description}
              type="text"
              onChange={onInputChange}
              errorShow={errorField["description"]}
            />
            <div
              className={style.gsf_star_container}
              style={{
                marginTop: "1.4rem",
              }}
            >
              {rating?.map((url, index) => (
                <img
                  key={index}
                  src={url}
                  alt="star icon"
                  onClick={() => onRating(index)}
                />
              ))}
            </div>
            <CreateButton label={"review"} onAction={onReviewUpdate} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AddBookReview;
